:root,
:root.theme-1 {


    // Background


    
    --mainBackground: hsl(222, 26%, 31%); 
    --toggleBackground: hsl(223, 31%, 20%); 
    --screenBackground: hsl(224, 36%, 15%); 



    //Keys

    --keyBackground: hsl(225, 21%, 49%); 
    --keyShadow: hsl(224, 28%, 35%); 

    --keyBackgroundToggle: hsl(6, 63%, 50%); 
    --keyShadow1: hsl(6, 70%, 34%);   

    --keyBackground2: hsl(30, 25%, 89%); 
    --keyShadow2: hsl(28, 16%, 65%);    


    // Text

    --textDark: hsl(221, 14%, 31%);
    --textlight: rgb(255, 255, 255);
    --textHead: rgb(255, 255, 255);
    --textEqual: rgb(255, 255, 255);
     


    //hover color

    --equalHover: hsl(6, 93%, 67%);
    --resetHover: hsl(225, 51%, 76%);
    --keyHover: hsl(0, 0%, 100%);

}
