


//  414 px, 640px, 1024px, 1400px
$breakpoints-up: ("mobile": "25.875em", "medium": "40em", "large": "64em", "xlarge": "87.5em");


// 639px, 1023px, 1399px
$breakpoints-down: ("small": "39.9374em", "medium": "63.9375em", "large": "87.437em");


@mixin breakpoint-up($size) {
    @media(max-width: map-get($breakpoints-up, $size)) {
        @content;
    }
}

@mixin breakpoint-down($size) {
    @media(min-width: map-get($breakpoints-down, $size)) {
        @content;
    }
}