html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-spartan);
  width: 100vw;
  height: 100vh;
}

h1, p, h2 {
  margin: 0;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --font-spartan: "Spartan", sans-serif;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 100vw;
}
@media (max-width: 25.875em) {
  .main {
    align-items: flex-start;
  }
}

.calculator {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 0.625rem;
  max-width: 33.75rem;
  max-height: 44.25rem;
}
@media (max-width: 25.875em) {
  .calculator {
    margin-top: 1.25rem;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 100%;
}

.main__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
}

.output {
  display: flex;
  align-items: center;
  justify-content: end;
  width: 100%;
  border-radius: 0.625rem;
  margin-top: 0.9375rem;
}
.output__display {
  font-size: 3rem;
  font-weight: bold;
  padding: 2.1875rem;
}
@media (max-width: 25.875em) {
  .output__display {
    font-size: 2rem;
    padding: 1.8125rem;
  }
}

.button__grid_container {
  display: grid;
  justify-content: center;
  margin-top: 1.5rem;
  padding: 2rem;
  max-width: 100%;
  border-radius: 0.625rem;
}
@media (max-width: 25.875em) {
  .button__grid_container {
    padding: 1.75rem;
  }
}

.button__container {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  grid-gap: 1.5625rem;
}
@media (max-width: 25.875em) {
  .button__container {
    grid-gap: 0.8125rem;
  }
}

:root,
:root.theme-1 {
  --mainBackground: hsl(222, 26%, 31%);
  --toggleBackground: hsl(223, 31%, 20%);
  --screenBackground: hsl(224, 36%, 15%);
  --keyBackground: hsl(225, 21%, 49%);
  --keyShadow: hsl(224, 28%, 35%);
  --keyBackgroundToggle: hsl(6, 63%, 50%);
  --keyShadow1: hsl(6, 70%, 34%);
  --keyBackground2: hsl(30, 25%, 89%);
  --keyShadow2: hsl(28, 16%, 65%);
  --textDark: hsl(221, 14%, 31%);
  --textlight: rgb(255, 255, 255);
  --textHead: rgb(255, 255, 255);
  --textEqual: rgb(255, 255, 255);
  --equalHover: hsl(6, 93%, 67%);
  --resetHover: hsl(225, 51%, 76%);
  --keyHover: hsl(0, 0%, 100%);
}

:root.theme-2 {
  --mainBackground: hsl(0, 0%, 90%);
  --toggleBackground: hsl(0, 5%, 81%);
  --screenBackground: hsl(0, 0%, 93%);
  --keyBackground: hsl(185, 42%, 37%);
  --keyShadow: hsl(185, 58%, 25%);
  --keyBackgroundToggle: hsl(25, 98%, 40%);
  --keyShadow1: hsl(25, 99%, 27%);
  --keyBackground2: hsl(45, 7%, 89%);
  --keyShadow2: hsl(35, 11%, 61%);
  --textDark: hsl(60, 10%, 19%);
  --textlight: #ffffff;
  --textHead: hsl(60, 10%, 19%);
  --textEqual: rgb(255, 255, 255);
  --equalHover: hsl(25, 100%, 61%);
  --resetHover:hsl(185, 40%, 56%);
  --keyHover: hsl(0, 0%, 100%);
}

:root.theme-3 {
  --mainBackground: #160628;
  --toggleBackground: #1d0934;
  --screenBackground: #1d0934;
  --keyBackground: #58077d;
  --keyShadow: #bc15f4;
  --keyBackgroundToggle: hsl(176, 100%, 44%);
  --keyShadow1: hsl(177, 92%, 70%);
  --keyBackground2: #341c4f;
  --keyShadow2: #871c9c;
  --textDark: hsl(52, 100%, 62%);
  --textHead: hsl(52, 100%, 62%);
  --textBlue: hsl(198, 20%, 13%);
  --textlight: rgb(255, 255, 255);
  --textEqual: #1d0934;
  --equalHover: #93FFF8;
  --resetHover:#8631AF;
  --keyHover: #6C34AC;
}

fieldset {
  position: relative;
  display: flex;
  justify-content: space-around;
  width: 4.4375rem;
  border: 0;
  text-align: center;
  padding: 0;
}
fieldset legend {
  display: inline-block;
  right: 4.6rem;
  top: 2.8rem;
  position: relative;
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
}
fieldset label {
  display: inline-block;
  position: relative;
  width: 1rem;
  padding-bottom: 2rem;
  z-index: 2;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: bold;
}
fieldset label:first-of-type {
  z-index: 1;
}
fieldset div {
  width: 4.4375rem;
}
fieldset input {
  position: absolute;
  top: 0;
  height: 100%;
  width: 33%;
  appearance: none;
  background: none;
  opacity: 0.000001;
}
fieldset label:first-of-type::before {
  content: "";
  position: absolute;
  width: 4.4375rem;
  height: 1.625rem;
  bottom: 0;
  left: -0.5rem;
  border-radius: 0.95rem;
}
fieldset label:last-of-type::after {
  content: "";
  position: absolute;
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  bottom: 0.3125rem;
  right: 0.9375rem;
  transition: transform 0.3s ease;
}
fieldset input:first-of-type:checked ~ div > label:last-of-type::after {
  transform: translateX(-1.6rem);
}
fieldset input:nth-of-type(2):checked ~ div > label:last-of-type::after {
  transform: translateX(-0.4rem);
}
fieldset input:last-of-type:checked ~ div > label:last-of-type::after {
  transform: translateX(0.8rem);
}

.button {
  min-width: 6.3125rem;
  height: 4rem;
  font-size: 2rem;
  font-weight: 700;
  border-radius: 0.625rem;
  letter-spacing: 0.125rem;
  border: none;
  font-family: var(--font-spartan);
  padding-bottom: 0.3125rem;
  cursor: pointer;
  transition: 300ms ease-in-out;
}
@media (max-width: 25.875em) {
  .button {
    min-width: 4rem;
    border-radius: 0.3125rem;
  }
}

.button__reset {
  grid-column-start: -5;
  grid-column-end: -3;
  grid-row-start: 5;
  grid-row-end: 6;
}
@media (max-width: 25.875em) {
  .button__reset {
    width: 3.125rem;
  }
}

.button__equal {
  grid-column-start: -3;
  grid-column-end: -1;
  grid-row-start: 5;
  grid-row-end: -1;
}

.button__del {
  font-size: 1.25rem;
}
@media (max-width: 25.875em) {
  .button__del {
    font-size: 1rem;
  }
}

.button--size {
  min-width: 14.1875rem;
  font-size: 1.25rem;
}
@media (max-width: 25.875em) {
  .button--size {
    min-width: 8.3125rem;
    font-size: 1rem;
  }
}

body {
  background-color: var(--mainBackground);
}

fieldset label:first-of-type::before {
  background-color: var(--toggleBackground);
}
fieldset label:last-of-type::after {
  background-color: var(--keyBackgroundToggle);
}
fieldset label:hover:last-of-type::before {
  background-color: blue;
}

.header__headline h1 {
  color: var(--textHead);
}
.header__toggle {
  color: var(--textHead);
}

.output {
  background-color: var(--screenBackground);
}
.output__display {
  color: var(--textHead);
}

.button__grid_container {
  background-color: var(--toggleBackground);
}

.button {
  color: var(--textDark);
  background-color: var(--keyBackground2);
  box-shadow: inset 0px -4px 0px var(--keyShadow2);
}
.button:hover {
  background-color: var(--keyHover);
}
.button__del {
  background-color: var(--keyBackground);
  color: var(--textlight);
  box-shadow: inset 0px -4px 0px var(--keyShadow);
}
.button__del:hover {
  background-color: var(--resetHover);
}
.button__reset {
  background-color: var(--keyBackground);
  color: var(--textlight);
  box-shadow: inset 0px -4px 0px var(--keyShadow);
}
.button__reset:hover {
  background-color: var(--resetHover);
}
.button__equal {
  background-color: var(--keyBackgroundToggle);
  color: var(--textEqual);
  box-shadow: inset 0px -4px 0px var(--keyShadow1);
}
.button__equal:hover {
  background-color: var(--equalHover);
}